import { useState, useEffect, createContext } from 'react'
import axios from 'axios'

import Price from '../logic/price'

export const DataConsumer = createContext()

export default function Data({children}){
    const [gameInfo, setGameInfo] = useState([]);
    const [activeGameInfo, setActiveGameInfo] = useState();
    const [isLogin, setIsLogin] = useState(false);
    const [err, setError] = useState();
    const [user, setUser] = useState();
    const [userCart, setUserCart] = useState([]);
    useEffect(() => {
        axios.get('https://steampages.herokuapp.com/game')
        .then(res=>res.data)
        .then(data=>setGameInfo(data))
    },[]) 
    useEffect(() => {   
        axios.get('https://steampages.herokuapp.com/user/login')
            .then(res => {
                const cartParsed = res.data?.cart?.map(item => JSON.parse(item))
                if (!res.data?.error){
                    setIsLogin(true)
                    setUser(res.data)
                    setUserCart([
                        ...userCart,
                        ...cartParsed
                    ])
                }            
            })
    }, [])
    const createUser=(e,userInfo)=>{
        e.preventDefault();
        axios.get('https://steampages.herokuapp.com/user/register',{params:userInfo})
            .then(res => {
                if (!res.data?.error) {
                    setIsLogin(true)
                    setUser(res.data)
                }
                else {
                    setError(res.data.error)
                }
            })
    }
    const login=(e,user)=> {
        e.preventDefault()
        axios.get('https://steampages.herokuapp.com/user/login',
            {
                params: user
            })
            .then(res => {
                if (!res.data?.error){
                    setIsLogin(true)
                    setUser(res.data)
                }
                else {
                    setError(res.data.error)
                }
            }) 
    }
    const logOut = () => {
        axios.get('https://steampages.herokuapp.com/user/logout')
        .then()
    }
    const updateUserCart = (newUser) => {
        if (!user) {
            return
        }
        axios.get('https://steampages.herokuapp.com/user/updateCart', { params: newUser })
        .then(res=>1)
    }
    const addCart = (newCart, e) => {
        e.preventDefault()
        setUserCart([...userCart, newCart])
        const newUser = {
            ...user,
            cart: [...userCart, newCart]
        }
        updateUserCart(newUser)
    }
    return (
        <DataConsumer.Provider
            value={{
                gameInfo,
                activeGameInfo,setActiveGameInfo,
                isLogin, setIsLogin,
                user, setUser,
                userCart,setUserCart,
                updateUserCart,
                Price,
                createUser, login, logOut,
                err,setError,
                addCart,
            }}
        >
            {children}
        </DataConsumer.Provider>
    )
}